.rc-checkbox__control {
    position: relative;
    inline-size: 25px;
    block-size: 25px;
    border: 1px solid;
    transition: border-color var(--transition-duration)
      var(--transition-timing-function);
  }

.rc-checkbox__control::after {
      opacity: 0;
      position: absolute;
      inset-block-start: 50%;
      inset-inline-start: 50%;
      inline-size: 15px;
      block-size: 13px;
      transform: translate(-50%, -50%);
      background-image: url("data:image/svg+xml,%3Csvg xmlns=%27http://www.w3.org/2000/svg%27 viewBox=%270 0 19 17%27%3E%3Cpolygon fill=%27%23d92e20%27 points=%277.134 16.466 0.588 10.574 1.926 9.088 6.866 13.534 16.817 0.598 18.402 1.817 7.134 16.466%27 /%3E%3C/svg%3E%0A");
      transition: opacity var(--transition-duration)
        var(--transition-timing-function);
      display: block;
      content: "";
    }

input[type="checkbox"]:checked ~ .rc-checkbox__control {
      border-color: var(--accent-color);
    }

input[type="checkbox"]:checked ~ .rc-checkbox__control::after {
        opacity: 1;
      }

input[type="checkbox"]:focus-visible ~ .rc-checkbox__control {
      outline: 2px solid var(--outline-color, currentcolor);
    }

.rc-checkbox__check {
