    height: 12px;
    animation: 2s linear 0s infinite normal none running rc-progress-rotate;
  }

.rc-progress__track {
    fill: transparent;
  }

.rc-progress__indicator {
    fill: transparent;
    stroke: hsla(0, 0%, 100%, 1);
    animation: 1.5s linear 0s infinite normal none running rc-progress-spin;
  }

@keyframes rc-progress-rotate {
  0% {
    transform: 0;
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes rc-progress-spin {
  0% {
    stroke-dasharray: 1px, 400px;
    stroke-dashoffset: 0px;
  }

  50% {
    stroke-dasharray: 400px, 400px;
    stroke-dashoffset: -100px;
  }

  100% {
    stroke-dasharray: 400px, 400px;
    stroke-dashoffset: -260px;
  }
}

