  transform: translateZ(0);
  transition: height 1s;
}

.rc_toast {
  --padding: clamp(20px, calc(20px + 10 * (100vw - 500px) / 254), 30px);
  inline-size: 100%;
  padding: calc(var(--padding) * 0.5) var(--padding);
  margin: 10px 0;
  background-color: hsla(0, 100%, 46%, 1);
  font-family: aktiv-grotesk, -apple-system, BlinkMacSystemFont, avenir next, avenir, segoe ui, helvetica neue, helvetica, Cantarell, Ubuntu, roboto, noto, arial, sans-serif;
  animation: popup 0.6s cubic-bezier(0.1, 1.06, 0.6, 1.34), hide 0.4s 3s;
}

.rc_toast__type {
    font-weight: bold;
  }

@keyframes popup {
  from {
    transform: translateY(50%);
    opacity: 0;
  }

  100% {
    transform: translateY(0%);
    opacity: 1;
  }
}

@keyframes hide {
  from {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

